<template>
  <div id="app-container">
    <div id="board-container">
      <Board @gameOver="updateScore"></Board>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import Board from "./components/Board.vue";

export default defineComponent({
  components: {
    Board,
  },
  data() {
    return {
      score: {
        player1: 0,
        player2: 0,
        draw: 0,
      },
    };
  },
  methods: {
    updateScore(e: number) {
      if (e === 1) {
        this.score.player1++;
      } else if (e === 2) {
        this.score.player2++;
      } else {
        this.score.draw++;
      }
    },
  },
});
</script>

<style>
#app-container {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
}
#board-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
</style>
