
import DxRadioGroup from "devextreme-vue/radio-group";
import DxButton from "devextreme-vue/button";
import { DxCheckBox } from "devextreme-vue/check-box";
import { AILevel } from "minimax";
import { defineComponent } from "vue";
export default defineComponent({
  components: {
    DxButton,
    DxCheckBox,
    DxRadioGroup,
  },
  emits: {
    resetGame: null,
    changeAiLevel: null,
    changeAiState: null,
  },
  methods: {
    changeAiState(e: { value: boolean }) {
      this.$emit("changeAiState", e.value);
    },
    changeAiLevel(e: { value: string }) {
      let level;
      switch (e.value) {
        case "Weak":
          level = AILevel.Weak;
          break;
        case "Normal":
          level = AILevel.Normal;
          break;
        case "Strong":
          level = AILevel.Strong;
          break;
      }
      this.$emit("changeAiLevel", level);
    },
  },

  computed: {
    getAiLevels() {
      return ["Weak", "Normal", "Strong"];
    },
    controlItemStyle() {
      return {
        margin: "5px",
      };
    },
  },
});
