
import { defineComponent } from "vue";
import BoardField from "./BoardField.vue";
import CoinInput from "./CoinInput.vue";
import ControlBar from "./ControlBar.vue";
import { alertController } from "@ionic/vue";
import { Board, AILevel } from "minimax";

export interface FieldState {
  rowIdx: number;
  colIdx: number;
  state: number; // 0, 1, 2 -> free, player 1, player 2
}

export default defineComponent({
  emits: {
    gameOver: null,
  },
  components: {
    BoardField,
    CoinInput,
    ControlBar,
  },
  data() {
    return {
      board: Array.from(Array(7), (_field: any, colIdx: number) =>
        Array.from(Array(6), (_field: any, rowIdx: number) => {
          return { rowIdx, colIdx, state: 0 };
        })
      ),
      locked: false,
      activePlayer: 1,
      remainingFields: 42,
      minimax: new Board(),
      aiOpponent: true,
      aiLevel: AILevel.Normal,
    };
  },
  methods: {
    resetBoard() {
      this.remainingFields = 0;
      for (const column of this.board) {
        for (const row of column) {
          row.state = 0;
          this.remainingFields++;
        }
      }
      this.activePlayer = 1;
      this.minimax.free();
      this.minimax = new Board();
      this.locked = false;
    },
    coinThrown(n: number) {
      const col = this.board[n - 1];
      let freeRow = 0;
      for (let row = 0; row < col.length; row++) {
        const field = col[row];
        if (field.state === 0) {
          freeRow = row;
          this.locked = true;
          this.animateColumn(col, col.length - 1, freeRow);
          break;
        }
      }
    },
    animateColumn(col: FieldState[], currentRow: number, freeRow: number) {
      if (currentRow < col.length - 1) {
        col[currentRow + 1].state = 0;
      }
      col[currentRow].state = this.activePlayer;
      if (currentRow > freeRow) {
        setTimeout(this.animateColumn, 100, col, currentRow - 1, freeRow);
      } else {
        setTimeout(this.finishTurn, 100, col[currentRow]);
      }
    },
    finishTurn(field: FieldState) {
      this.minimax.makeMove(field.colIdx, this.activePlayer);
      if (!this.checkWinCondition(field, this.activePlayer)) {
        this.activePlayer = this.activePlayer === 1 ? 2 : 1;
        if (this.aiOpponent) {
          if (this.activePlayer === 2) {
            this.aiMove();
          } else {
            this.locked = false;
          }
        } else {
          this.locked = false;
        }
      } else {
        // game finished;
      }
    },
    aiMove() {
      console.log("AI ", this.aiLevel, "is making a move!");
      const move = this.minimax.select_move(this.aiLevel);
      if (move != null) {
        this.coinThrown(move + 1);
      } else {
        throw new Error("AI didn't find a suitable move!");
      }
    },
    checkWinCondition(lastTurn: FieldState, player: number): boolean {
      // horizontally
      let chips =
        1 +
        this.checkRecursive(lastTurn, 0, 1) +
        this.checkRecursive(lastTurn, 0, -1);
      if (chips >= 4) {
        this.gameOver(player);
        return true;
      }
      // vertically
      chips =
        1 +
        this.checkRecursive(lastTurn, 1, 0) +
        this.checkRecursive(lastTurn, -1, 0);
      if (chips >= 4) {
        this.gameOver(player);
        return true;
      }
      // diagonally up
      chips =
        1 +
        this.checkRecursive(lastTurn, 1, 1) +
        this.checkRecursive(lastTurn, -1, -1);
      if (chips >= 4) {
        this.gameOver(player);
        return true;
      }
      chips =
        1 +
        this.checkRecursive(lastTurn, 1, -1) +
        this.checkRecursive(lastTurn, -1, 1);
      if (chips >= 4) {
        this.gameOver(player);
        return true;
      }
      this.remainingFields--;
      console.log(this.remainingFields);
      if (this.remainingFields === 0) {
        this.gameOver(0);
        return true;
      }
      return false;
    },
    checkRecursive(
      lastTurn: FieldState,
      rowDir: number,
      colDir: number
    ): number {
      if (
        lastTurn.colIdx + colDir < 0 ||
        lastTurn.colIdx + colDir >= this.board.length ||
        lastTurn.rowIdx + rowDir < 0 ||
        lastTurn.rowIdx + rowDir >= this.board[0].length
      ) {
        return 0;
      }
      const field = this.board[lastTurn.colIdx + colDir][
        lastTurn.rowIdx + rowDir
      ];
      if (field.state === lastTurn.state) {
        return 1 + this.checkRecursive(field, rowDir, colDir);
      } else {
        return 0;
      }
    },
    gameOver(winner: number) {
      this.locked = true;
      if (winner === 0) {
        this.presentGameFinished("The game has ended in a draw!");
      } else {
        if (this.aiOpponent) {
          if (winner === 1) {
            this.presentGameFinished(`You have won!`);
          } else {
            this.presentGameFinished(`You have lost!`);
          }
        } else {
          this.presentGameFinished(`Player ${winner} has won!`);
        }
      }
      this.$emit("gameOver", winner);
    },
    async presentGameFinished(msg: string) {
      const alert = await alertController.create({
        header: msg,
        message: "Do you want to start a new game?",
        buttons: [
          {
            text: "Yes",
            handler: () => {
              this.resetBoard();
            },
          },
          "No",
        ],
      });
      return alert.present();
    },
  },
  computed: {
    fieldStyle: function () {
      const width =
        window.innerHeight > window.innerWidth
          ? window.innerWidth
          : window.innerHeight;
      console.log(window.innerWidth, window.innerHeight);
      console.log(width);
      return {
        width: `${width / 10}px`,
        height: `${width / 10}px`,
      };
    },
  },
});
