<template>
  <div class="field" @mouseover="hover = true" @mouseleave="hover = false">
    <div
      :class="{
        'shadow-coin': !hover && !locked,
        coin: hover && !locked,
        'player-a': !locked && activePlayer === 1,
        'player-b': !locked && activePlayer === 2,
      }"
    ></div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
  props: {
    colIdx: null,
    locked: { type: Boolean },
    activePlayer: { type: Number },
  },
  data() {
    return {
      hover: false,
    };
  },
});
</script>

<style scoped>
.shadow-coin {
  width: 100%;
  height: 100%;
  background-color: beige;
  border-radius: 50%;
}
</style>
