<template>
  <div id="control-bar">
    <DxButton
      :style="controlItemStyle"
      @click="$emit('resetGame')"
      text="New Game"
    ></DxButton>
    <DxCheckBox
      value="true"
      :style="controlItemStyle"
      @valueChanged="changeAiState"
      text="AI"
    ></DxCheckBox>

    <DxRadioGroup
      :style="controlItemStyle"
      :items="getAiLevels"
      :value="getAiLevels[1]"
      @valueChanged="changeAiLevel"
      layout="horizontal"
    />
  </div>
</template>

<script lang="ts">
import DxRadioGroup from "devextreme-vue/radio-group";
import DxButton from "devextreme-vue/button";
import { DxCheckBox } from "devextreme-vue/check-box";
import { AILevel } from "minimax";
import { defineComponent } from "vue";
export default defineComponent({
  components: {
    DxButton,
    DxCheckBox,
    DxRadioGroup,
  },
  emits: {
    resetGame: null,
    changeAiLevel: null,
    changeAiState: null,
  },
  methods: {
    changeAiState(e: { value: boolean }) {
      this.$emit("changeAiState", e.value);
    },
    changeAiLevel(e: { value: string }) {
      let level;
      switch (e.value) {
        case "Weak":
          level = AILevel.Weak;
          break;
        case "Normal":
          level = AILevel.Normal;
          break;
        case "Strong":
          level = AILevel.Strong;
          break;
      }
      this.$emit("changeAiLevel", level);
    },
  },

  computed: {
    getAiLevels() {
      return ["Weak", "Normal", "Strong"];
    },
    controlItemStyle() {
      return {
        margin: "5px",
      };
    },
  },
});
</script>

<style>
#control-bar {
  display: flex;
  flex-direction: column;
  align-items: center;
}
</style>